import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import { api } from '@/config/common.js'
import { query, create } from '@/api/common.js'
export default {
	components: {
		cuPanel,
		cuCell,
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '采购退款',
			businessType: 0,
			reqData: {
				order: {
					billtype: 2,
					accountid: '',
					contactunitid: '',
					payaccountid: '',
					amount: 0.00,
					isprint: 0,
					status: 1
				},
				orderlist: []
			},
			cashAccountDict: [],
			disableSubmit: true
		};
	},
	onLoad(options) {
		if (options) {
			this.businessType = options.businessType
			if (this.businessType == 1) {
				this.title = '销售退款'
			}
			let data = JSON.parse(options.reqData)
			this.reqData.order.contactunitid = data.contactunitid
			this.reqData.order.amount = parseFloat(data.totalPrice).toFixed(2)
			this.reqData.orderlist = data.productList
		}
		this.$refs.loading.open()
		query(api.cashAccount).then(res => {
			this.$refs.loading.close()
			if (res.status == 200 && res.data.returnCode == '0000') {
				this.cashAccountDict = res.data.data.resultList
			}
		}).catch(error => {
			this.$refs.loading.close()
		})
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleCashAccountChange(val) {
			if (this.businessType == 0) {
				this.reqData.order.payaccountid = val.detail.value
			} else {
				this.reqData.order.accountid = val.detail.value
			}
		},
		handleStatusChange(val) {
			this.reqData.order.status = val.detail.value
		},
		handlePrintChange(val) {
			this.reqData.order.isprint = val.detail.value
		},
		handleSubmit() {
			if (this.businessType == 0) {
				this.$refs.loading.open()
				create(api.purPurchaseOrder, this.reqData).then(res => {
					this.$refs.loading.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						uni.showToast({
							icon: 'success',
							title: '提交成功'
						})
						setTimeout(()=>{
							let pages =  getCurrentPages()
							let prevPage = pages[pages.length - 2]
							prevPage.setData({
								commandType: 'success',
							})
							uni.navigateBack({
								delta: 1
							})
						},500)
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			} else {
				this.$refs.loading.open()
				create(api.salesOrder, this.reqData).then(res => {
					this.$refs.loading.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						uni.showToast({
							icon: 'success',
							title: '提交成功'
						})
						setTimeout(()=>{
							let pages =  getCurrentPages()
							let prevPage = pages[pages.length - 2]
							prevPage.setData({
								commandType: 'success',
							})
							uni.navigateBack({
								delta: 1
							})
						},500)
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			}
		}
	},
	watch:{
		'reqData.order': {
			handler(val) {
				if (val.accountid || val.payaccountid) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
